import React from 'react'

/**
 * 结果
 */
class Results extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			show: false
		};
	}
	render() {
		return <div className={`bunny-results ${this.isShown() ? 'bunny-results-show' : ''}`} style={{
			height: `calc(100vh - ${this.getHeaderHeight()}px)`
		}}>
			<span className='bunny-results-close'
				onClick={this.onResultCloseClicked} />
			<div className='bunny-results-demo'>
				<span>前面都是骗你的.</span>
			</div>
		</div>
	}
	getHeaderHeight() {
		return this.props.headerHeight;
	}
	show() {
		this.setState({
			show: true
		});
	}
	hide() {
		this.setState({
			show: false
		})
	}
	isShown() {
		return this.state.show;
	}
	onResultCloseClicked = () => {
		this.hide();
	}
}

export default Results


