import React, { Component } from 'react';

import './icon-font/fontello.css'
import './App.css'
import Chat from './components/chat'
import Results from './components/results'

/**
 * BUNNY-GIRL APP ENTRANCE
 */
class App extends Component {
	render() {
		return <div className='bunny' style={{
			height: `calc(100vh - ${this.getHeaderHeight()}px)`
		}}>
			<Chat headerHeight={this.getHeaderHeight()} app={this} />
			<Results headerHeight={this.getHeaderHeight()} app={this} ref='results'/>
		</div>
	}
	getHeaderHeight() {
		return this.props.headerHeight;
	}
	showResults() {
		this.refs.results.show();
	}
}

export default App;



