import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

class BunnyG {
	paint(node) {
		const headerHeight = node.getAttribute('data-header-height');
		ReactDOM.render(<App headerHeight={headerHeight} />, node);
	}
	destroy(node) {
		ReactDOM.unmountComponentAtNode(node);
	}
}

window.BunnyG = BunnyG;

const root = document.getElementById('bunny-root');
new BunnyG().paint(root);

registerServiceWorker();

export default BunnyG;


