import React from 'react'
import AIMessage from './ai-message'

/**
 * Thinking message from AI
 */
class AIThinkMessage extends AIMessage {
	getClassName() {
		return 'bunny-msg-ai bunny-msg-thinking';
	}
	getThinkingMsg() {
		return <span>
			<span />
			<span />
			<span />
		</span>
	}
	getDialogue() {
		return {
			msg: this.getThinkingMsg()
		};
	}
}

export default AIThinkMessage


