import React from 'react'

/**
 * 消息
 */
class Message extends React.Component {
	renderLight() {
		const dialogue = this.getDialogue();
		if (dialogue.result) {
			return <span className='bunny-msg-light bg-light' />;
		}
	}
	render() {
		const dialogue = this.getDialogue();
		return <div className={`bunny-msg ${this.getClassName()}`}>
			<div className='bunny-msg-avatar'>
			</div>
			<div className='bunny-msg-body'>
				{this.renderLight()}
				<span className='bunny-msg-msg'>{dialogue.msg}</span>
				<span className='bunny-msg-timestamp'>{this.convertTimestamp(dialogue.timestamp)}</span>
			</div>
		</div>
	}
	getClassName() {
		return '';
	}
	getDialogue() {
		return this.props.dialogue;
	}
	convertTimestamp(timestamp) {
		return timestamp ? `${this.padRight(timestamp.getHours())}:${this.padRight(timestamp.getMinutes())}` : '';
	}
	padRight(v) {
		return v < 10 ? `0${v}` : v;
	}
}

export default Message


